.clr:after, .wrap:after, [class*=" col-"]:after, [class^=col-]:after {
  content: '.';
  display: block;
  width: 100%;
  height: 0;
  overflow: hidden;
  font-size: 0;
  clear: both; }

.section--video:after, .section--video:before {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0; }

